import { useEffect, useRef } from "react";

export const useEffectWhen = (callback: () => void, when: boolean) => {
  const isCalled = useRef(false);
  const cb = useRef(callback);

  useEffect(() => {
    cb.current = callback;
  });

  useEffect(() => {
    if (when && !isCalled.current) {
      isCalled.current = true;
      cb.current();
    }
  }, [when]);
};
