import { useCallback, useState } from "react";

import storages, { StorageKeys, StoragesNames } from "utils/storages";

import { useDidUpdate } from "./useDidUpdate";

type PersistedStateOptions = {
  storageName: StoragesNames;
  expireIn: number;
};

export function usePersistedState<T>(
  key: StorageKeys,
  defaultValue: T,
  options?: Partial<PersistedStateOptions>
): [T, React.Dispatch<React.SetStateAction<T>>, () => void] {
  const { storageName = "local", expireIn = Infinity } = options ?? {};

  const [persistedState, setPersistedState] = useState<T>(
    () =>
      storages[storageName].get<T>(key)?.value ??
      storages[storageName].set<T>(key, defaultValue, { expireIn }).value
  );

  useDidUpdate(() => {
    setPersistedState(defaultValue);
  }, [defaultValue]);

  useDidUpdate(() => {
    storages[storageName].set(key, persistedState, { expireIn });
  }, [persistedState]);

  const clearPersistedState = useCallback(
    () => storages[storageName].remove(key),
    [key, storageName]
  );

  return [persistedState, setPersistedState, clearPersistedState];
}
