import React from "react";
import { BrowserRouter, Navigate, Route, Routes } from "react-router-dom";

import Header from "components/Header";
import Main from "layouts/Main";
import HomePage from "pages/HomePage";
import StudyTestPage from "pages/StudyTestPage";
import StudyTestResultPage from "pages/StudyTestResultPage";

import { routes } from "./AppRouter.types";

const AppRouter: React.FC = () => {
  return (
    <BrowserRouter>
      <Main
        header={<Header />}
        main={
          <Routes>
            <Route path={routes.home} element={<HomePage />} />
            <Route path={routes.tests} element={<StudyTestPage />} />
            <Route path={routes.studyTestResult} element={<StudyTestResultPage />} />
            <Route path="*" element={<Navigate to={routes.home} />} />
          </Routes>
        }
      ></Main>
    </BrowserRouter>
  );
};

export default AppRouter;
