export type RouteNames = "home" | "tests" | "studyTestResult";

export const routes: Record<RouteNames, string> = {
  home: "/test-constructor/",
  tests: "/test-constructor/study-test/:id",
  studyTestResult: "/test-constructor/study-test/result/:id",
};

export interface RouteGetters {
  home: () => string;
  tests: (params: { id: string }) => string;
  studyTestResult: (params: { id: string }) => string;
}
