import { makeAutoObservable } from "mobx";

import { TestModel, TestModule } from "store/Test/TestModel";
import javascriptTests from "store/__stubs/javascript";

import { TestsMap, TestsModel } from "./TestsModel";

const tests = javascriptTests;

export class Tests implements Readonly<TestsModel> {
  private tests = tests.reduce<TestsMap>(
    (tests, test) => ({ ...tests, [test.id]: test }),
    {} as TestsMap
  );

  private testToModules = tests.reduce<Record<TestModule, TestModel[]>>(
    (tests, test) => ({
      ...tests,
      [test.module]: tests[test.module].concat(test),
    }),
    { javascript: [], github: [] }
  );

  testsList = tests;

  constructor() {
    makeAutoObservable(this);
  }

  get testToModulesList(): TestsModel["testToModulesList"] {
    return Object.entries(this.testToModules) as TestsModel["testToModulesList"];
  }

  getTestById = (testId: string): TestModel | null => {
    return this.tests?.[testId] ?? null;
  };
}
