import React from "react";
import AppRouter from "router/AppRouter";

import AuthProvider from "providers/AuthProvider";
import ThemeProvider from "providers/ThemeProvider";

const App: React.FC = () => {
  return (
    <ThemeProvider>
      <AuthProvider>
        <AppRouter />
      </AuthProvider>
    </ThemeProvider>
  );
};

export default App;
