import { Button, Divider, Grid, Paper, Stack, Typography } from "@mui/material";
import clsx from "clsx";
import isEqual from "lodash.isequal";
import React from "react";
import { useNavigate, useParams } from "react-router-dom";

import Question from "components/Question";

import { useDidMount } from "hooks/useDidMount";
import { useStore } from "hooks/useStore";

import { getPath } from "utils/getPath";

import { useStyles } from "./StudyTestResult.styles";

const StudyTestResult: React.FC = () => {
  const classes = useStyles();
  const { id: testId = "" } = useParams<{ id: string }>();
  const navigate = useNavigate();

  const { user, tests } = useStore();

  useDidMount(() => {
    if (!testId) navigate(getPath("home"));
  });

  const test = tests.getTestById(testId);
  const completedTest = user.getCompletedTestById(testId);

  const restartTest = () => navigate(getPath("tests", { id: testId }));

  if (!test || !completedTest) return null;

  return (
    <Grid container spacing={3}>
      {test.questions.map((question) => {
        const isRightAnswered = isEqual(
          question.correctAnswers,
          completedTest.answers[question.id]
        );

        return (
          <Grid key={question.id} item xs={12} sm={9} md={6} mx="auto">
            <Paper
              className={clsx(classes.paper, {
                [classes.paperSuccess]: isRightAnswered,
                [classes.paperError]: !isRightAnswered,
              })}
            >
              <Question
                mode="view"
                question={question}
                values={completedTest.answers[question.id]}
              />
              {!isRightAnswered && question.hintText && (
                <Stack direction="column" spacing={2} mx={2}>
                  <Divider sx={{ borderColor: "error.main", mt: 2 }} />
                  <Typography variant="subtitle1">{question.hintText}</Typography>
                </Stack>
              )}
            </Paper>
          </Grid>
        );
      })}
      <Grid item xs={12} container justifyContent="center">
        <Button variant="contained" onClick={restartTest}>
          Пройти заново
        </Button>
      </Grid>
    </Grid>
  );
};

export default StudyTestResult;
