import { AppBar, Box, Container, Toolbar } from "@mui/material";
import React from "react";

export interface MainProps {
  header: JSX.Element;
  main: JSX.Element;
  footer?: JSX.Element;
}

const Main: React.FC<MainProps> = ({ header, main, footer }) => {
  return (
    <Box py={2} px={1} minHeight="100vh" bgcolor="background.default">
      <Box pb={6}>
        <AppBar position="fixed" component="div">
          <Toolbar
            variant="dense"
            sx={{
              display: "flex",
              alignItems: "center",
              maxHeight: 48,
              minHeight: 48,
              height: 48,
            }}
          >
            {header}
          </Toolbar>
        </AppBar>
      </Box>

      <Container maxWidth="lg" component="main">
        {main}
      </Container>

      {footer && <footer>{footer}</footer>}
    </Box>
  );
};

export default Main;
