import { Divider, Grid, Typography } from "@mui/material";
import React from "react";

import TestCard from "../TestCard";
import { TestsListProps } from "./TestsList.types";

const TestsList: React.FC<TestsListProps> = ({
  testsList,
  user,
  unfinishedTest,
  filterStatus,
  filterValue,
  testToModulesList,
}) => {
  if (filterStatus === "all" && !filterValue.length) {
    return (
      <>
        {testToModulesList.map(([module, tests]) => (
          <React.Fragment key={module}>
            {!!tests.length && (
              <>
                <Grid item xs={12}>
                  <Typography variant="h6" color="text.primary" gutterBottom>
                    {module}
                  </Typography>
                  <Divider />
                </Grid>
                {tests.map((test) => (
                  <Grid key={test.id} item xs={12} sm={6} md={4} lg={3}>
                    <TestCard
                      test={test}
                      user={user}
                      isUnfinished={unfinishedTest === test}
                      isCompleted={!!user.getCompletedTestById(test.id)}
                    />
                  </Grid>
                ))}
              </>
            )}
          </React.Fragment>
        ))}
      </>
    );
  }

  let filteredTests = testsList;

  if (filterStatus === "complete") {
    filteredTests = filteredTests.filter((test) => !!user.getCompletedTestById(test.id));
  } else if (filterStatus === "uncomplete") {
    filteredTests = filteredTests.filter((test) => !user.getCompletedTestById(test.id));
  }

  if (filterValue.length) {
    filteredTests = filteredTests.filter((test) =>
      test.header.toLowerCase().includes(filterValue.toLocaleLowerCase())
    );
  }

  return (
    <>
      {filteredTests.map((test) => (
        <Grid key={test.id} item xs={12} sm={6} md={4} lg={3}>
          <TestCard
            test={test}
            user={user}
            isUnfinished={unfinishedTest === test}
            isCompleted={!!user.getCompletedTestById(test.id)}
          />
        </Grid>
      ))}
    </>
  );
};

export default TestsList;
