import { makeAutoObservable } from "mobx";

import storages from "utils/storages";

import {
  CompletetdTests,
  UserCompletedTest,
  UserConstructopRarams,
  UserModel,
  UserTest,
} from "./UserModel";

export class User implements Readonly<UserModel> {
  private name;
  private surname;
  private completedTests = {} as CompletetdTests;
  unfinishedTest;

  constructor(params: UserConstructopRarams) {
    makeAutoObservable(this);

    this.name = params.name;
    this.surname = params.surname;
    this.unfinishedTest =
      storages.local.get<UserTest>("unfinished-test")?.value ?? ({} as UserTest);
    this.completedTests =
      storages.local.get<CompletetdTests>("completed-tests")?.value ?? ({} as CompletetdTests);
  }

  get fullname(): string | null {
    if (!this.name || !this.surname) return null;
    return `${this.name} ${this.surname}`;
  }

  get unfinishedTestId(): string | null {
    return Object.keys(this.unfinishedTest ?? {})?.[0] ?? null;
  }

  setUnfinishedTest = (unfinishedTest: UserTest) => {
    this.unfinishedTest = unfinishedTest;
  };

  removeUnfinishedTest = (testId: string) => {
    delete this.unfinishedTest[testId];
  };

  addCompletedTest = (testId: string, completedTest: UserCompletedTest) => {
    this.completedTests[testId] = completedTest;

    storages.local.set<CompletetdTests>("completed-tests", this.completedTests, {
      expireIn: Infinity,
    });
  };

  getCompletedTestById = (testId: string): UserCompletedTest | null => {
    return this.completedTests[testId] ?? null;
  };

  removeCompletedTestById = (finishedTestId: string) => {
    if (!this.completedTests[finishedTestId]) return;

    delete this.completedTests[finishedTestId];
    storages.local.set<CompletetdTests>("completed-tests", this.completedTests, {
      expireIn: Infinity,
    });
  };
}
