import { ChevronLeft, ChevronRight } from "@mui/icons-material";
import {
  Box,
  Button,
  Grid,
  IconButton,
  Stepper as MUIStepper,
  Step,
  StepButton,
} from "@mui/material";
import React from "react";

import { StepperProps } from "./Stepper.types";

const Stepper: React.FC<StepperProps> = ({
  children,
  nextQuestion,
  prevQuestion,
  setActiveQuestion,
  completeQuestion,
  questions,
  isLastQuestion,
  isActiveQuestionCompleted,
  activeQuestionIndex,
  isChosenAnswerVariant,
  completedQuestions,
}) => {
  return (
    <>
      <MUIStepper nonLinear alternativeLabel activeStep={activeQuestionIndex} sx={{ mb: 3 }}>
        {questions.map((question, index) => (
          <Step key={question.text} completed={Boolean(completedQuestions?.[index])}>
            <StepButton
              disableTouchRipple
              color="inherit"
              onClick={() => setActiveQuestion(index)}
            />
          </Step>
        ))}
      </MUIStepper>

      <Box position="relative" overflow="hidden">
        {children}
      </Box>

      <Grid container alignItems="center" pt={4} spacing={0.5}>
        <Grid item xs={2} container justifyContent="center">
          <IconButton onClick={prevQuestion}>
            <ChevronLeft />
          </IconButton>
        </Grid>
        <Grid item xs={8}>
          <Button
            fullWidth
            onClick={completeQuestion}
            variant="contained"
            size="small"
            disabled={isActiveQuestionCompleted || !isChosenAnswerVariant}
          >
            {isLastQuestion ? "Закончить" : "Ответить"}
          </Button>
        </Grid>
        <Grid item xs={2} container justifyContent="center">
          <IconButton onClick={nextQuestion}>
            <ChevronRight />
          </IconButton>
        </Grid>
      </Grid>
    </>
  );
};

export default Stepper;
