import { Button, Grid, TextField } from "@mui/material";
import React, { createContext, useState } from "react";

import Login from "layouts/Login";

import { usePersistedState } from "hooks/usePersistedState";

import { AuthContext as AuthContextType, Roles, passphrases } from "./AuthProvider.types";

const AuthContext = createContext<AuthContextType>({
  isLoggedIn: false,
  logout: () => {
    throw new Error("error logout in AuthContext");
  },
});

const phraseAndRoles: Record<passphrases, Roles> = {
  student: "student",
};

const AuthProvider: React.FC<{ children: JSX.Element }> = ({ children }) => {
  const [role, setRole] = usePersistedState<Roles | null>("role", null, {
    expireIn: Date.now() + 1000 * 60 * 60 * 7,
  });
  const [passphrase, setPassphrase] = useState("");
  const [error, setError] = useState("");

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => setPassphrase(e.target.value);

  const signIn = (e: React.MouseEvent) => {
    e.preventDefault();
    if (passphrase && passphrase in phraseAndRoles) {
      setRole(phraseAndRoles[passphrase as passphrases]);
      setError("");
    } else {
      setError("uncorrect passphrase");
    }
  };

  return (
    <AuthContext.Provider value={{ isLoggedIn: Boolean(role), logout: () => setRole(null) }}>
      {Boolean(role) && children}

      {!Boolean(role) && (
        <Login>
          <Grid container spacing={1} maxWidth="250px" margin="auto">
            <Grid item xs={12}>
              <TextField
                fullWidth
                required
                value={passphrase}
                id="passphrase"
                label="passphrase"
                variant="outlined"
                onChange={handleChange}
                error={!!error}
                helperText={error}
              />
            </Grid>
            <Grid item xs={12}>
              <Button fullWidth variant="contained" type="submit" onClick={signIn}>
                sign in
              </Button>
            </Grid>
          </Grid>
        </Login>
      )}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
