import isEqual from "lodash.isequal";

import { Answers } from "store/Test";

export const calculateQuantityOfRightAnswers = (
  answers: Answers,
  correctAnswers: Answers
): number =>
  Object.entries(answers).reduce<number>((quantity, [key, value]) => {
    if (isEqual(correctAnswers[key], value)) {
      quantity++;
    }
    return quantity;
  }, 0);

export const filterFalsyAnswers = (answers: Answers): Answers =>
  Object.fromEntries(
    Object.entries(answers).map(([questionId, userAnswer]) => [
      questionId,
      Object.entries(userAnswer).reduce(
        (acc, [answerId, answerValue]) => (answerValue ? { ...acc, [answerId]: answerValue } : acc),
        {}
      ),
    ])
  );
