import {
  CssBaseline,
  ThemeProvider as MUIThemeProvider,
  createTheme,
  useMediaQuery,
} from "@mui/material";
import React, { createContext, useContext, useMemo } from "react";

import { usePersistedState } from "hooks/usePersistedState";

import { ThemeContext as ThemeContextType, ThemeVariants } from "./ThemeProvider.types";

const ThemeContext = createContext<ThemeContextType>({
  mode: "light",
  toggleColorMode: () => {
    throw new Error("error toggleColorMode in ThemeContext");
  },
});

export const useTheme = (): ThemeContextType => useContext(ThemeContext);

const ThemeProvider: React.FC<{ children: JSX.Element }> = ({ children }) => {
  const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
  const [mode, setMode] = usePersistedState<ThemeVariants>(
    "theme",
    prefersDarkMode ? "dark" : "light"
  );

  const colorMode = useMemo(
    () => ({
      toggleColorMode: () => {
        setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
      },
      mode,
    }),
    [mode, setMode]
  );

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode,
        },
      }),
    [mode]
  );

  return (
    <ThemeContext.Provider value={colorMode}>
      <CssBaseline />
      <MUIThemeProvider theme={theme}>{children}</MUIThemeProvider>
    </ThemeContext.Provider>
  );
};

export default ThemeProvider;
