import { Chip, Divider, Grid, TextField, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";

import { usePersistedState } from "hooks/usePersistedState";
import { useStore } from "hooks/useStore";

import { TestFilters } from "./Home.types";
import TestCard from "./TestCard";
import TestsList from "./TestsList";

const initialFilterValues: TestFilters = {
  filterValue: "",
  filterStatus: "all",
};

const statuses = ["all", "complete", "uncomplete"];

const statusesNames: Record<string, string> = {
  all: "Все",
  complete: "Законченные",
  uncomplete: "Незаконченные",
};

const Home: React.FC = () => {
  const [{ filterValue, filterStatus }, setFilterValues] = usePersistedState<TestFilters>(
    "test-filters",
    initialFilterValues,
    {
      storageName: "session",
    }
  );

  const { tests, user } = useStore();

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) =>
    setFilterValues((prevFilterValues) => ({ ...prevFilterValues, filterValue: e.target.value }));

  const makeHandleChipClick = (filterStatus: TestFilters["filterStatus"]) => () =>
    setFilterValues((prevFilterValues) => ({ ...prevFilterValues, filterStatus }));

  const unfinishedTest = user.unfinishedTestId ? tests.getTestById(user.unfinishedTestId) : null;

  return (
    <>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            size="small"
            id="filterValue"
            name="filterValue"
            placeholder="Фильтровать по названию"
            value={filterValue}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          {statuses.map((status) => (
            <Chip
              sx={{ mr: 2 }}
              key={status}
              label={statusesNames[status]}
              variant={status === filterStatus ? "filled" : "outlined"}
              color="primary"
              onClick={makeHandleChipClick(status as TestFilters["filterStatus"])}
            />
          ))}
        </Grid>
      </Grid>

      <Grid container spacing={2} mt={1}>
        {unfinishedTest && (
          <>
            <Grid item xs={12}>
              <Typography color="text.primary" variant="h6">
                Незаконченный
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Divider />
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={3}>
              <TestCard test={unfinishedTest} user={user} isUnfinished isCompleted={false} />
            </Grid>
            <Grid item xs={12}>
              <Divider />
            </Grid>
          </>
        )}
        <TestsList
          user={user}
          filterValue={filterValue}
          filterStatus={filterStatus}
          testsList={tests.testsList}
          testToModulesList={tests.testToModulesList}
          unfinishedTest={unfinishedTest}
        />
      </Grid>
    </>
  );
};

export default observer(Home);
