import { makeAutoObservable } from "mobx";

import { Answers, QuestionModel, TestModel, TestModelParams } from "./TestModel";

export class Test implements Readonly<TestModel> {
  id;
  header;
  module;
  subheader;
  questions;
  completedQuestions = {} as TestModel["completedQuestions"];
  activeQuestionIndex = 0;

  constructor(data: TestModelParams) {
    makeAutoObservable(this);

    this.id = data.id;
    this.module = data.module;
    this.header = data.header;
    this.subheader = data.subheader;
    this.questions = data.questions;
  }

  get correctAnswers(): Answers {
    return this.questions.reduce<Answers>(
      (correctAnswers, question) => ({ ...correctAnswers, [question.id]: question.correctAnswers }),
      {}
    );
  }

  get totalQuestions(): number {
    return this.questions.length;
  }

  get activeQuestion(): QuestionModel {
    return this.questions[this.activeQuestionIndex];
  }

  get completedQeustionsCount(): number {
    return Object.keys(this.completedQuestions).length;
  }

  get isAllQuestionsCompleted(): boolean {
    return this.totalQuestions === this.completedQeustionsCount;
  }

  get isLastQuestion(): boolean {
    return this.completedQeustionsCount === this.totalQuestions - 1;
  }

  get isActiveQuestionCompleted(): boolean {
    return this.completedQuestions?.[this.activeQuestionIndex] ?? false;
  }

  nextQuestion = () => {
    this.activeQuestionIndex =
      this.activeQuestionIndex === this.totalQuestions - 1 ? 0 : this.activeQuestionIndex + 1;
  };

  prevQuestion = () => {
    this.activeQuestionIndex =
      this.activeQuestionIndex === 0 ? this.totalQuestions - 1 : this.activeQuestionIndex - 1;
  };

  setActiveQuestion = (questionIndex: number) => {
    this.activeQuestionIndex = questionIndex;
  };

  completeQuestion = () => {
    this.completedQuestions[this.activeQuestionIndex] = true;
    this.nextQuestion();
  };

  resetCompletedQuestions = () => {
    this.completedQuestions = {};
  };
}
