import { RouteGetters, RouteNames, routes } from "router/AppRouter.types";

export type GetPath = (
  route: RouteNames,
  ...params: Parameters<RouteGetters[RouteNames]>
) => string;

export const getPath: GetPath = (route, ...params) => {
  if (!params.length) return routes[route];

  const updatedRoute = Object.entries(params[0]).reduce<string>(
    (formattedRoute, [key, value]: [string, string | number]) =>
      formattedRoute.replace(`:${key}`, `${value}`),
    routes[route]
  );

  return updatedRoute;
};
