import { Box, Fade, Paper } from "@mui/material";
import clsx from "clsx";
import { observer } from "mobx-react-lite";
import React from "react";
import { useNavigate, useParams } from "react-router-dom";

import { UserTest } from "store/User/UserModel";

import Question from "components/Question";

import { useDidMount } from "hooks/useDidMount";
import { useEffectWhen } from "hooks/useEffectWhen";
import { usePersistedState } from "hooks/usePersistedState";
import { useStore } from "hooks/useStore";
import { useWillUnmount } from "hooks/useWillUnmount";

import { getPath } from "utils/getPath";

import Stepper from "./Stepper";
import { useStyles } from "./StudyTest.styles";
import { calculateQuantityOfRightAnswers, filterFalsyAnswers } from "./utils";

const initialValues: UserTest = {};

const StudyTest: React.FC = () => {
  const classes = useStyles();
  const { id: testId = "" } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [values, setValues, clearValues] = usePersistedState("unfinished-test", initialValues, {
    expireIn: new Date().getTime() + 24 * 60 * 60 * 1000,
  });

  const { tests, user } = useStore();

  const test = tests.getTestById(testId);

  useDidMount(() => {
    if (!test) {
      navigate(getPath("home"));
    } else {
      user.removeCompletedTestById(testId);
    }
  });

  useEffectWhen(() => {
    if (!test) return;

    const filteredAnswers = filterFalsyAnswers(values[testId]);

    user.addCompletedTest(testId, {
      answers: filteredAnswers,
      quantityOfRightAnswers: calculateQuantityOfRightAnswers(filteredAnswers, test.correctAnswers),
    });

    navigate(getPath("studyTestResult", { id: testId }));
  }, test?.isAllQuestionsCompleted ?? false);

  useWillUnmount(() => {
    if (!test) return;

    clearValues();
    user.removeUnfinishedTest(testId);
    test.resetCompletedQuestions();
  });

  if (!test) return null;

  const {
    nextQuestion,
    prevQuestion,
    setActiveQuestion,
    completeQuestion,
    questions,
    isLastQuestion,
    isActiveQuestionCompleted,
    activeQuestionIndex,
    completedQuestions,
    activeQuestion: { id: questionId },
  } = test;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, type, value, checked } = e.target;
    setValues((prevValues) => ({
      [testId]: {
        ...(prevValues[testId] ?? {}),
        [questionId]: {
          ...(prevValues[testId]?.[questionId] ?? {}),
          [name]: type === "checkbox" ? checked : value,
        },
      },
    }));
  };

  const isChosenAnswerVariant = Object.entries(values[testId]?.[questionId] ?? {}).length > 0;

  return (
    <Paper elevation={2} sx={{ p: 3 }}>
      <Stepper
        activeQuestionIndex={activeQuestionIndex}
        nextQuestion={nextQuestion}
        prevQuestion={prevQuestion}
        setActiveQuestion={setActiveQuestion}
        completeQuestion={completeQuestion}
        questions={questions}
        completedQuestions={completedQuestions}
        isLastQuestion={isLastQuestion}
        isActiveQuestionCompleted={isActiveQuestionCompleted}
        isChosenAnswerVariant={isChosenAnswerVariant}
      >
        <>
          {questions.map((question) => (
            <Fade
              key={question.id}
              in={question === questions[activeQuestionIndex]}
              unmountOnExit
              mountOnEnter
            >
              <Box
                className={clsx({
                  [classes.notActiveQuestion]: question !== questions[activeQuestionIndex],
                })}
              >
                <Question
                  question={question}
                  disableFields={isActiveQuestionCompleted}
                  values={values[testId]?.[questionId] ?? {}}
                  onChange={handleChange}
                />
              </Box>
            </Fade>
          ))}
        </>
      </Stepper>
    </Paper>
  );
};

export default observer(StudyTest);
