import {
  Box,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormLabel,
  Radio,
  RadioGroup,
  Typography,
} from "@mui/material";
import React from "react";

import { useStyles } from "./Question.styles";
import { QuestionProps } from "./Question.types";

const Question: React.FC<QuestionProps> = ({
  question,
  onChange,
  values,
  disableFields,
  mode = "form",
}) => {
  const classes = useStyles();
  return (
    <Box display="flex" justifyContent="center">
      <FormControl component="fieldset" variant="standard">
        <FormLabel component="legend">
          <Typography color="text.primary" align="center" variant="h6">
            {question.text}
          </Typography>
          {question.imgSrc && (
            <div className={classes.imgContainer}>
              <img alt={question.text} src={question.imgSrc} />
            </div>
          )}
        </FormLabel>
        <Box display="flex" justifyContent="center" mt={2}>
          <FormGroup>
            {question.type === "checkbox" &&
              question.answerOptions.map(({ name, label, id }) => (
                <FormControlLabel
                  key={id}
                  id={id}
                  name={name}
                  disabled={disableFields}
                  control={
                    <Checkbox
                      onChange={onChange}
                      checked={!!values[name]}
                      color={
                        mode === "form"
                          ? "primary"
                          : question.correctAnswers[name] === values[name]
                          ? "success"
                          : "error"
                      }
                      size="small"
                    />
                  }
                  label={label}
                />
              ))}

            {question.type === "radio" && (
              <RadioGroup value={values[question.answerOptions[0].name] ?? ""}>
                {question.answerOptions.map(({ value, name, label, id }) => (
                  <FormControlLabel
                    key={id}
                    id={id}
                    name={name}
                    value={value}
                    disabled={disableFields}
                    control={
                      <Radio
                        onChange={onChange}
                        color={
                          mode === "form"
                            ? "primary"
                            : question.correctAnswers[name] === values[name]
                            ? "success"
                            : "error"
                        }
                        size="small"
                      />
                    }
                    label={label}
                  />
                ))}
              </RadioGroup>
            )}
          </FormGroup>
        </Box>
      </FormControl>
    </Box>
  );
};

export default Question;
