import { PauseCircleOutline } from "@mui/icons-material";
import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Divider,
  Rating,
  Typography,
} from "@mui/material";
import clsx from "clsx";
import React from "react";
import { useNavigate } from "react-router-dom";

import { getPath } from "utils/getPath";

import { useStyles } from "./TestCard.styles";
import { TestCardProps } from "./TestCard.types";

const TestCard: React.FC<TestCardProps> = ({ test, user, isUnfinished, isCompleted }) => {
  const classes = useStyles();
  const navigate = useNavigate();

  const startTest = () => navigate(getPath("tests", { id: test.id }));

  const showTestResult = () => navigate(getPath("studyTestResult", { id: test.id }));

  // const rating = isCompleted
  //   ? (user.getCompletedTestById(test.id)?.quantityOfRightAnswers ?? 0 / test.totalQuestions) * 5
  //   : 0;

  return (
    <Card
      className={clsx(classes.card, {
        [classes.cardUncompleted]: isUnfinished,
        [classes.cardCompleted]: isCompleted,
      })}
    >
      {isUnfinished && (
        <Box position="absolute" top="8px" right="8px">
          <PauseCircleOutline color="warning" />
        </Box>
      )}
      <CardContent>
        <Typography sx={{ maxWidth: "90%" }} gutterBottom variant="h6">
          {test.header}
        </Typography>
        {!isCompleted && (
          <Typography gutterBottom variant="subtitle2" color="text.secondary">
            Количество вопросов {test.totalQuestions}
          </Typography>
        )}
        {isCompleted && (
          <Typography gutterBottom variant="subtitle2" color="text.secondary">
            Правильных ответов {user.getCompletedTestById(test.id)?.quantityOfRightAnswers} /
            Количество вопросов {test.totalQuestions}
          </Typography>
        )}
        {/* <Divider sx={{ my: 2 }} />
        <Typography gutterBottom variant="subtitle1" color="text.secondary">
          Result rating
        </Typography>
        <Rating precision={0.5} value={rating} readOnly disabled={!isCompleted} /> */}
      </CardContent>
      <CardActions>
        {isCompleted && (
          <Button
            sx={{ ml: "auto" }}
            size="small"
            color="info"
            variant="outlined"
            onClick={showTestResult}
          >
            Результат
          </Button>
        )}
        <Button
          sx={{ ml: isCompleted ? 1 : "auto" }}
          size="small"
          color={isUnfinished ? "warning" : "primary"}
          variant="contained"
          onClick={startTest}
        >
          {!isUnfinished && (isCompleted ? "Заново" : "Начать")}
          {isUnfinished && "Продолжить"}
        </Button>
      </CardActions>
    </Card>
  );
};

export default TestCard;
