export type LocalStorageKeys = "user" | "unfinished-test" | "completed-tests" | "theme" | "role";
export type SessionStorageKeys = "test-filters";

export type StoragesNames = "local" | "session";
export type StorageKeys = LocalStorageKeys | SessionStorageKeys;

interface StorageValue<T> {
  value: T;
  expireIn?: number;
}

interface StorageOptions {
  expireIn: number;
}

interface StorageAdapter<K extends StorageKeys> {
  get<T>(key: K): StorageValue<T> | null;
  set<T>(key: K, value: T, options: StorageOptions): StorageValue<T>;
  remove(key: K): void;
}

const isExpired = (ms?: number): boolean => {
  if (!ms) return false;

  const dateNow = new Date().getTime();
  const dateExpiration = new Date(ms).getTime();

  return dateExpiration <= dateNow;
};

class LocalStorageAdapter<K extends LocalStorageKeys> implements StorageAdapter<K> {
  get<T>(key: K): StorageValue<T> | null {
    const valueFromStorage = window?.localStorage?.getItem(key);
    if (!valueFromStorage) return null;

    let parsedValue: StorageValue<T> | null;

    try {
      parsedValue = JSON.parse(valueFromStorage);
    } catch (err) {
      parsedValue = null;
    }

    if (isExpired(parsedValue?.expireIn)) {
      this.remove(key);
      parsedValue = null;
    }

    return parsedValue;
  }

  set<T>(key: K, value: T, options: StorageOptions): StorageValue<T> {
    const nextValue: StorageValue<T> = { value, ...options };
    const stringifyNextValue = JSON.stringify(nextValue);

    window?.localStorage?.setItem(key, stringifyNextValue);
    return nextValue;
  }

  remove(key: K) {
    window?.localStorage?.removeItem(key);
  }
}

class SessionStorageAdapter<K extends SessionStorageKeys> implements StorageAdapter<K> {
  get<T>(key: K): StorageValue<T> | null {
    const valueFromStorage = window?.sessionStorage?.getItem(key);
    if (!valueFromStorage) return null;

    let parsedValue: StorageValue<T> | null;

    try {
      parsedValue = JSON.parse(valueFromStorage);
    } catch (err) {
      parsedValue = null;
    }

    if (isExpired(parsedValue?.expireIn)) {
      this.remove(key);
      parsedValue = null;
    }

    return parsedValue;
  }

  set<T>(key: K, value: T, options: StorageOptions): StorageValue<T> {
    const nextValue: StorageValue<T> = { value, ...options };
    const stringifyNextValue = JSON.stringify(nextValue);

    window?.sessionStorage?.setItem(key, stringifyNextValue);
    return nextValue;
  }

  remove(key: K) {
    window?.sessionStorage?.removeItem(key);
  }
}

const storages: Record<StoragesNames, StorageAdapter<StorageKeys>> = {
  local: new LocalStorageAdapter(),
  session: new SessionStorageAdapter(),
};

export default storages;
